const hotSpotArrow = (hotSpotDiv) => {
    const imageDiv = document.createElement('img')
    imageDiv.setAttribute('width', '100%')
    imageDiv.setAttribute('height', '100%')
    imageDiv.setAttribute('src', arrow)
    hotSpotDiv.appendChild(imageDiv)
}

