import React, { useState, useEffect, useRef } from 'react'
import { useSpring, animated } from 'react-spring'
import styled from 'styled-components'
import './App.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faMapMarkerAlt, faHome } from '@fortawesome/free-solid-svg-icons'
import logo from './img/logo.png'
import Pan from './components/Pan'
import Gallery from './components/Gallery'
import { Map } from './components/Map'

const Wrapper = styled.div`
    /*   cursor: 'pointer';
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 100%;
    margin: 0px;
    */
`

const PanSceneWrapper = styled(animated.div)`
        position absolute;
        z-index:0;
      `

const Opening = styled(animated.div)`
    position: absolute;
    width: 100vw;
    height: 100vh;
    margin: 0px;
    overflow: hidden;
    background-color: #fff;
    z-index: 300;
`

const Logo = styled.img`
    position: relative;
    width: 350px;
    left: calc(50% - 175px);
    top: calc(40% - 90px);
    @media (max-width: 768px) {
        width: 50%;
        top: 40%;
        left: 25%;
    }
    margin: none;
    border: none;
    z-index: 310;
`

const Mapbutton = styled.button`
    cursor: 'pointer';
    display: flex;
    align-items: center;
    position: absolute;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.2);
    border: 1px solid #fff;
    letter-spacing: 2px;
    border-radius: 5px;
    padding: 0px 6px;
    z-index: 20;
`

const Homebutton = styled.button`
    cursor: 'pointer';
    display: flex;
    align-items: center;
    position: absolute;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.2);
    border: 1px solid #fff;
    letter-spacing: 2px;
    border-radius: 50%;
    padding: 0px 6px;
    z-index: 20;
`

let mobile =
    /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
        navigator.userAgent
    )
        ? true
        : false

function App({ lang }) {
    const [isMobile] = useState(mobile)

    const [ABShift, setABShift] = useState(false) //true=>A;false=>B

    const [imgIndex, setImgIndex] = useState(0)
    const [imgAIndex, setImgAIndex] = useState(0)
    const [imgBIndex, setImgBIndex] = useState(0)

    //nowYaw
    const [nowYaw, setNowYaw] = useState(0) //for map marker

    const [goToYaw, setGoToYaw] = useState(0)
    const [goToYawA, setGoToYawA] = useState(0)
    const [goToYawB, setGoToYawB] = useState(0)

    const [goToPitch, setGoToPitch] = useState(0)
    const [goToPitchA, setGoToPitchA] = useState(0)
    const [goToPitchB, setGoToPitchB] = useState(0)

    const [GalleryHide, setGalleryHide] = useState(false)

    //const [overviewMode, setOverviewMode] = useState(false)
    const [planMode, setPlanMode] = useState('') // '', med, large

    // const [lookAtCard, setLookAtCard] = useState(false)
    // const [cardType, setCardType] = useState('')
    //const [cardContent, setCardContent] = useState('')

    const [panLoad, setPanLoad] = useState(false) //for opening
    const [opening, setOpening] = useState(true)

    // const [langCh, setLangCh] = useState(lang)
    //const [controlGuide, setControlGuide] = useState(false)

    const panA = useRef(null)
    const panB = useRef(null)

    const naviRef = useRef(null)

    const openingProps = useSpring({
        opacity: opening ? 1 : 0,
        config: { duration: 300 },
    })
    const panSceneAWrapperProps = useSpring({
        opacity: ABShift ? 1 : 0,
        config: { duration: 250 },
    })
    const panSceneBWrapperProps = useSpring({
        opacity: ABShift ? 0 : 1,
        config: { duration: 250 },
    })

    let OpeningClose = () =>
        setTimeout(() => {
            setOpening(false)
        }, 250)

    useEffect(() => {
        if (panLoad) {
            OpeningClose()
        }
        clearTimeout(OpeningClose)
    }, [panLoad])

    useEffect(() => {
        if (!ABShift) {
            setGoToYawB(goToYaw)
            setGoToPitchB(goToPitch)
        } else {
            setGoToYawA(goToYaw)
            setGoToPitchA(goToPitch)
        }
    }, [ABShift, goToYaw, goToPitch])

    useEffect(() => {
        if (ABShift) {
            imgIndex === imgBIndex
                ? setABShift((c) => !c)
                : setImgBIndex(imgIndex)
        } else {
            imgIndex === imgAIndex
                ? setABShift((c) => !c)
                : setImgAIndex(imgIndex)
        }
    }, [imgIndex])

    const hotSpotsClickHandler = (type, args, card) => {
        //console.log(type, args,machine)
        switch (type) {
            case 'GOTO':
                console.log(args)
                setGoToYaw(args[1])
                setGoToPitch(args[2])
                setImgIndex(args[0])
                break
            case 'LOOKAT':
                // if (!ABShift) {
                //     panoA.current.getViewer().lookAt(...args)
                // } else {
                //     panoB.current.getViewer().lookAt(...args)
                // }
                // setCardType(card[0])
                // setCardContent(card[1])
                // setLookAtCard((c) => !c)
                break
            default:
                break
        }
    }

    return (
        <Wrapper>
            <Opening
                style={{
                    ...openingProps,
                    visibility: openingProps.opacity.to((o) =>
                        o === 0 ? 'hidden' : 'visible'
                    ),
                }}
            >
                <Logo src={logo} />
            </Opening>

            <Mapbutton
                style={{ transform: 'translate3d(0, 20px, 0)', left: '1%' }}
                onClick={() => {
                    planMode !== '' ? setPlanMode('') : setPlanMode('med')
                    if (isMobile) {
                        planMode !== '' ? setPlanMode('') : setPlanMode('med')
                    } else {
                        planMode !== '' ? setPlanMode('') : setPlanMode('large')
                    }
                }}
            >
                <FontAwesomeIcon
                    icon={faMapMarkerAlt}
                    size="2x"
                    style={{ padding: '5px' }}
                />
                地圖
            </Mapbutton>

            <Homebutton
                style={{
                    transform: 'translate3d(0, 65px, 0)',
                    left: '1%',
                    width: '36px',
                    height: '36px',
                }}
                onClick={() => {
                    setImgIndex(0)
                    setGoToYaw(0)
                    setGoToPitch(-15)
                }}
            >
                <FontAwesomeIcon icon={faHome} size="lg" />
            </Homebutton>

            {planMode && (
                <Map
                    planMode={planMode}
                    setPlanMode={setPlanMode}
                    imgIndex={imgIndex}
                    setImgIndex={setImgIndex}
                    setGoToYaw={setGoToYaw}
                    setGoToPitch={setGoToPitch}
                    nowYaw={nowYaw}
                />
            )}

            {/*Scene A*/}
            <PanSceneWrapper
                style={{
                    ...panSceneAWrapperProps,
                    visibility: panSceneAWrapperProps.opacity.to((o) =>
                        o === 0 ? 'hidden' : 'visible'
                    ),
                    zIndex: panSceneAWrapperProps.opacity.to((o) =>
                        o === 0 ? 0 : 10
                    ), //important!
                }}
            >
                <Pan
                    isMobile={isMobile}
                    pan={panA}
                    imgIndex={imgAIndex}
                    panLoad={panLoad}
                    setPanLoad={setPanLoad}
                    goToYaw={goToYawA}
                    goToPitch={goToPitchA}
                    //setLookAtCard={setLookAtCard}
                    planMode={planMode}
                    setPlanMode={setPlanMode}
                    setNowYaw={setNowYaw}
                    hotSpotsClickHandler={hotSpotsClickHandler}
                    setABShift={setABShift}
                />
            </PanSceneWrapper>

            {/*Scene B*/}
            <PanSceneWrapper
                style={{
                    ...panSceneBWrapperProps,
                    visibility: panSceneBWrapperProps.opacity.to((o) =>
                        o === 0 ? 'hidden' : 'visible'
                    ),
                    zIndex: panSceneBWrapperProps.opacity.to((o) =>
                        o === 0 ? 0 : 10
                    ), //important!
                }}
            >
                <Pan
                    isMobile={isMobile}
                    pan={panB}
                    imgIndex={imgBIndex}
                    panLoad={panLoad}
                    setPanLoad={setPanLoad}
                    goToYaw={goToYawB}
                    goToPitch={goToPitchB}
                    //setLookAtCard={setLookAtCard}
                    planMode={planMode}
                    setPlanMode={setPlanMode}
                    setNowYaw={setNowYaw}
                    hotSpotsClickHandler={hotSpotsClickHandler}
                    setABShift={setABShift}
                />
            </PanSceneWrapper>

            <Gallery
                naviRef={naviRef}
                GalleryHide={GalleryHide}
                setGalleryHide={setGalleryHide}
                setImgIndex={setImgIndex}
                setGoToYaw={setGoToYaw}
                setGoToPitch={setGoToPitch}
                imgIndex={imgIndex}
            />
        </Wrapper>
    )

    // return (
    //     <PanoSceneWrapper
    //         style={{
    //             ...panoSceneAWrapperProps,
    //             visibility: panoSceneAWrapperProps.opacity.to((o) =>
    //                 o === 0 ? 'hidden' : 'visible'
    //             ),
    //             zIndex: panoSceneAWrapperProps.opacity.to((o) =>
    //                 o === 0 ? 0 : 10
    //             ), //important!
    //         }}
    //     >
    //         <Pan
    //             isMobile={isMobile}
    //             pan={panA}
    //             imgIndex={imgAIndex}
    //             panLoad={panLoad}
    //             setPanLoad={setPanLoad}
    //             goToYaw={goToYawA}
    //             goToPitch={goToPitchA}
    //             //setLookAtCard={setLookAtCard}
    //             //planMode={planMode}
    //             //setPlanMode={setPlanMode}
    //             setNowYaw={setNowYaw}
    //             hotSpotsClickHandler={hotSpotsClickHandler}
    //             setABShift={setABShift}
    //         />
    //     </PanoSceneWrapper>
    // )
}

export default App
