import React from 'react'
import ImageGallery from 'react-image-gallery'
import { useSpring, animated } from 'react-spring'
import styled from 'styled-components'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
    faArrowCircleDown,
    faArrowCircleUp,
} from '@fortawesome/free-solid-svg-icons'
import { sences } from '../Config/galleryConfig.js'
import '../css/image-gallery.css'

const sencesConfig = sences

const GalleryWrap = styled(animated.div)`
    cursor: 'pointer';
    position: fixed;
    bottom: 0;
    width: 100%;
    background: #333;
    z-index: 200;
`
const Gallerybutton = styled.button`
    cursor: 'pointer';
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    width: 100%;
    top: -19px;
    color: #fff;
    z-index: 20;
    &:hover {
        transform: scale(1.1);
        border: none;
        outline: none;
    }
    &:active,
    &:focus,
    &:visited {
        border: none;
        outline: none;
    }
`

const Gallery = (props) => {
    const {
        naviRef,
        GalleryHide,
        setGalleryHide,
        setImgIndex,
        setGoToYaw,
        setGoToPitch,
        imgIndex,
    } = props

    const GalleryHideProps = useSpring({
        bottom: GalleryHide ? `-${naviRef.current.clientHeight}px` : '0px',
        config: {
            mass: 1,
            tension: 200,
            friction: 40,
        },
    })

    return (
        <GalleryWrap ref={naviRef} style={GalleryHideProps}>
            <Gallerybutton onClick={() => setGalleryHide(!GalleryHide)}>
                {' '}
                {!GalleryHide && (
                    <FontAwesomeIcon icon={faArrowCircleDown} size="lg" />
                )}{' '}
                {GalleryHide && (
                    <FontAwesomeIcon icon={faArrowCircleUp} size="lg" />
                )}{' '}
            </Gallerybutton>
            <ImageGallery
                items={sencesConfig()}
                showNav={true}
                onThumbnailClick={(_, index) => {
                    setImgIndex(sencesConfig()[index].args[0])
                    setGoToYaw(sencesConfig()[index].args[1])
                    setGoToPitch(sencesConfig()[index].args[2])
                }}
                showFullscreenButton={false}
                showPlayButton={false}
                autoPlay={false}
                infinite={true}
                slideDuration={0}
                slideInterval={0}
                lazyLoad={true}
                disableThumbnailScroll={false}
                disableSwipe={false}
                disableKeyDown={true}
                slideOnThumbnailOver={true}
                onBeforeSlide={(nextIndex, index) => {
                    setImgIndex(sencesConfig()[nextIndex].args[0])
                    setGoToYaw(sencesConfig()[nextIndex].args[1])
                    setGoToPitch(sencesConfig()[nextIndex].args[2])
                }}
            />
        </GalleryWrap>
    )
}

export default Gallery
