import React, { useState, useEffect, useRef } from 'react'
import { useSpring, animated } from 'react-spring'
import styled from 'styled-components'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faStreetView, faWindowClose } from '@fortawesome/free-solid-svg-icons'
import { mapimg, mark } from '../Config/mapConfig.js'

const markConfig = mark

const MapWrap = styled(animated.div)`
    position: absolute;
    left: 1%;
    top: 0;
    transform: translate3d(0, 62px, 0);
    z-index: 21;
    &::-webkit-scrollbar-track {
        border-radius: 3px;
        background-color: #f5f5f5;
    }
    &::-webkit-scrollbar {
        height: 3vw;
        max-height: 12px;
        width: 3vw;
        max-width: 12px;
        background-color: #f5f5f5;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 3px;
        -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
        background-color: #555;
    }
    &::-webkit-scrollbar-corner {
        background: #f5f5f5;
    }
`
const Mapbg = styled.div`
    background: url(${mapimg}) no-repeat;
    background-size: cover;
    width: 1000px;
    height: 800px;
    position: relative;
`

const MarkerWrap = styled.div`
    position: absolute;
    width: 2vw;
    min-width: 10px;
    max-width: 20px;
    height: 2vw;
    min-height: 10px;
    max-height: 20px;
`

const Marker = styled.div`
    cursor: 'pointer';
    width: 100%;
    height: auto;
    top: 0%;
    left: 0%;
    border: none;
    position: relative;
    &:hover {
        filter: invert(100%);
        transform: scale(1.5, 1.5);
    }
    z-index: 26;
`

const Closebutton = styled.button`
    cursor: 'pointer';
    position: absolute;
    width: 2rem;
    border: none;
    z-index: 21;
    &:hover {
        transform: scale(1.1);
    }
`

const Map = (props) => {
    const {
        planMode,
        setPlanMode,
        imgIndex,
        setImgIndex,
        setGoToYaw,
        setGoToPitch,
    } = props

    const [scrollXY, setScrollXY] = useState([0, 0])

    const mapPlanRef = useRef(null)

    const [mapPlanProps, setMapPlanProps] = useSpring(() => ({
        width: '0vw',
        height: '0vw',
        overflow: 'hidden',
    }))

    useEffect(() => {
        if (planMode === 'med') {
            setMapPlanProps({
                width: '77vw',
                height: '77vw',
                overflow: 'scroll',
            })
        } else if (planMode === 'large') {
            setMapPlanProps({
                width: '42vw',
                height: '35.3vw',
                overflow: 'scroll',
            })
        } else {
            setMapPlanProps({ width: '0vw', height: '0vw', overflow: 'hidden' })
        }
    }, [planMode])

    useEffect(() => {
        let offSetTop = parseInt(markConfig[imgIndex].top) / 100
        let offSetLeft = parseInt(markConfig[imgIndex].left) / 100
        mapPlanRef.current.scrollTop =
            mapPlanRef.current.scrollHeight * offSetTop -
            mapPlanRef.current.clientHeight * 0.5
        mapPlanRef.current.scrollLeft =
            mapPlanRef.current.scrollWidth * offSetLeft -
            mapPlanRef.current.clientWidth * 0.5
    }, [imgIndex, planMode])

    return (
        <MapWrap
            //minWidth, minHeight for mobile device
            style={{
                ...mapPlanProps,
                // minWidth: planMode === 'med' ? '500px' : null,
                // minHeight: planMode === 'med' ? '500px' : null,
            }}
            ref={mapPlanRef}
            onScroll={() => {
                setScrollXY([
                    mapPlanRef.current.scrollTop,
                    mapPlanRef.current.scrollLeft,
                ])
            }}
        >
            <Mapbg>
                {markConfig.map((data, i) => {
                    return (
                        <MarkerWrap
                            style={{ top: data.top, left: data.left }}
                            key={i}
                            onClick={() => {
                                setGoToYaw(data.args[1])
                                setGoToPitch(data.args[2])
                                setImgIndex(data.args[0])
                            }}
                        >
                            <Marker
                                style={{
                                    filter:
                                        imgIndex === i
                                            ? 'opacity(100%) saturate(1)'
                                            : 'opacity(50%) saturate(0)',
                                }}
                            >
                                <FontAwesomeIcon
                                    icon={faStreetView}
                                    size="2x"
                                    style={{
                                        color: 'red',
                                    }}
                                />
                            </Marker>
                        </MarkerWrap>
                    )
                })}
            </Mapbg>

            <Closebutton
                style={{ top: scrollXY[0] + 5, left: scrollXY[1] + 5 }}
                onClick={() => {
                    setPlanMode('')
                }}
            >
                <FontAwesomeIcon icon={faWindowClose} size="2x" />
            </Closebutton>
        </MapWrap>
    )
}

export { Map }
