import React from 'react'
import { Pannellum } from 'pannellum-react'
import { sences, hotspot } from '../Config/panConfig'
import uuid from 'uuid'

const [sencesUrl, hotspotConfig] = [sences, hotspot]

const Panscene = (props) => {
    const {
        isMobile,
        pan,
        imgIndex,
        setPanLoad,
        goToYaw,
        goToPitch,
        //setLookAtCard,
        setNowYaw,
        hotSpotsClickHandler,
        setABShift,
    } = props

    // console.log(props)

    const hotspotsdata = hotspotConfig.filter(
        (data) => data.imgindex === imgIndex
    )

    const hotspots = hotspotsdata.map((data) => (
        <Pannellum.Hotspot
            key={uuid()}
            type={data.type}
            pitch={data.pitch}
            yaw={data.yaw}
            cssClass={data.cssClass}
            tooltip={data.tooltip}
            text={data.text}
            URL={data.URL}
            handleClickArg={data.handleClickArg}
            handleClick={(evt, arg) => {
                hotSpotsClickHandler(arg.type, arg.args, arg.card)
            }}
        />
    ))

    return (
        <Pannellum
            ref={pan}
            id={uuid()}
            width="100vw"
            height="100vh"
            image={isMobile ? sencesUrl[imgIndex] : sencesUrl[imgIndex]}
            yaw={imgIndex === 0 ? -14 : goToYaw}
            //yaw={goToYaw}
            // pitch={goToPitch}
            pitch={imgIndex === 0 ? -15 : goToPitch}
            hfov={isMobile ? 63 : 130}
            //hfov={imgIndex === 0 ? 110 : 100}
            autoLoad={true}
            //autoRotate={true}
            showZoomCtrl={false}
            showFullscreenCtrl={false}
            howControls={true}
            compass={false}
            hotspotDebug={true}
            onLoad={() => {
                console.log('onLoad')
                setPanLoad(true)
                //setLookAtCard(false)
                setABShift((c) => !c)
            }}
            onRender={() => {
                setNowYaw(pan.current.getViewer().getYaw())
                //console.log('onRender')
                //console.log(pan.current.getViewer().getConfig())
                //console.log(pan.current.props.image)
                //console.log(pan.current.props.yaw)
            }}
        >
            {hotspots}
        </Pannellum>
    )
}

export default Panscene
